// import "./../style/visual.less";
import { select as d3Select } from "d3-selection";
import { scaleLinear, scaleBand } from "d3-scale";
import { axisBottom } from "d3-axis";
import "regenerator-runtime/runtime";
import * as tms from "powerbi-visuals-utils-formattingutils";
var textMeasurementService = tms.textMeasurementService;
import { getValue, getCategoricalObjectValue } from "./objectEnumerationUtility";
import { dataViewWildcard } from "powerbi-visuals-utils-dataviewutils";
let defaultSettings = {
    enableAxis: {
        show: false,
        fill: "#000000",
    }
};
/**
 * Function that converts queried data into a view model that will be used by the visual.
 *
 * @function
 * @param {VisualUpdateOptions} options - Contains references to the size of the container
 *                                        and the dataView which contains all the data
 *                                        the visual had queried.
 * @param {IVisualHost} host            - Contains references to the host which contains services
 */
function visualTransform(options, host) {
    let dataViews = options.dataViews;
    let viewModel = {
        dataPoints: [],
        dataMax: 0,
        settings: {}
    };
    if (!dataViews
        || !dataViews[0]
        || !dataViews[0].categorical
        || !dataViews[0].categorical.categories
        || !dataViews[0].categorical.categories[0].source
        || !dataViews[0].categorical.values) {
        return viewModel;
    }
    let categorical = dataViews[0].categorical;
    let category = categorical.categories[0];
    let dataValue = categorical.values[0];
    let barChartDataPoints = [];
    let dataMax;
    let colorPalette = host.colorPalette;
    let objects = dataViews[0].metadata.objects;
    const strokeColor = getColumnStrokeColor(colorPalette);
    let barChartSettings = {
        enableAxis: {
            show: getValue(objects, 'enableAxis', 'show', defaultSettings.enableAxis.show),
            fill: getAxisTextFillColor(objects, colorPalette, defaultSettings.enableAxis.fill),
        }
    };
    const strokeWidth = getColumnStrokeWidth(colorPalette.isHighContrast);
    for (let i = 0, len = Math.max(category.values.length, dataValue.values.length); i < len; i++) {
        const color = getColumnColorByIndex(category, i, colorPalette);
        const selectionId = host.createSelectionIdBuilder()
            .withCategory(category, i)
            .createSelectionId();
        barChartDataPoints.push({
            color,
            strokeColor,
            strokeWidth,
            selectionId,
            value: dataValue.values[i],
            category: `${category.values[i]}`,
        });
    }
    dataMax = dataValue.maxLocal;
    return {
        dataPoints: barChartDataPoints,
        dataMax: dataMax,
        settings: barChartSettings,
    };
}
function getColumnColorByIndex(category, index, colorPalette) {
    if (colorPalette.isHighContrast) {
        return colorPalette.background.value;
    }
    const defaultColor = {
        solid: {
            color: colorPalette.getColor(`${category.values[index]}`).value,
        }
    };
    return getCategoricalObjectValue(category, index, 'colorSelector', 'fill', defaultColor).solid.color;
}
function getColumnStrokeColor(colorPalette) {
    return colorPalette.isHighContrast
        ? colorPalette.foreground.value
        : null;
}
function getColumnStrokeWidth(isHighContrast) {
    return isHighContrast
        ? 2
        : 0;
}
function getAxisTextFillColor(objects, colorPalette, defaultColor) {
    if (colorPalette.isHighContrast) {
        return colorPalette.foreground.value;
    }
    return getValue(objects, "enableAxis", "fill", {
        solid: {
            color: defaultColor,
        }
    }).solid.color;
}
export class BarChart {
    /**
     * Creates instance of BarChart. This method is only called once.
     *
     * @constructor
     * @param {VisualConstructorOptions} options - Contains references to the element that will
     *                                             contain the visual and a reference to the host
     *                                             which contains services.
     */
    constructor(options) {
        this.host = options.host;
        this.svg = d3Select(options.element)
            .append('svg')
            .classed('barChart', true);
        this.barContainer = this.svg
            .append('g')
            .classed('barContainer', true);
        this.xAxis = this.svg
            .append('g')
            .classed('xAxis', true);
    }
    /**
     * Updates the state of the visual. Every sequential databinding and resize will call update.
     *
     * @function
     * @param {VisualUpdateOptions} options - Contains references to the size of the container
     *                                        and the dataView which contains all the data
     *                                        the visual had queried.
     */
    update(options) {
        let viewModel = visualTransform(options, this.host);
        let settings = this.barChartSettings = viewModel.settings;
        this.barDataPoints = viewModel.dataPoints;
        let width = options.viewport.width;
        let height = options.viewport.height;
        this.svg
            .attr("width", width)
            .attr("height", height);
        if (settings.enableAxis.show) {
            let margins = BarChart.Config.margins;
            height -= margins.bottom;
        }
        this.xAxis
            .style("font-size", Math.min(height, width) * BarChart.Config.xAxisFontMultiplier)
            .style("fill", settings.enableAxis.fill);
        let yScale = scaleLinear()
            .domain([0, viewModel.dataMax])
            .range([height, 0]);
        let xScale = scaleBand()
            .domain(viewModel.dataPoints.map(d => d.category))
            .rangeRound([0, width])
            .padding(0.2);
        let xAxis = axisBottom(xScale);
        const colorObjects = options.dataViews[0] ? options.dataViews[0].metadata.objects : null;
        this.xAxis.attr('transform', 'translate(0, ' + height + ')')
            .call(xAxis)
            .attr("color", getAxisTextFillColor(colorObjects, this.host.colorPalette, defaultSettings.enableAxis.fill));
        const textNodes = this.xAxis.selectAll("text");
        BarChart.wordBreak(textNodes, xScale.bandwidth(), height);
        this.barSelection = this.barContainer
            .selectAll('.bar')
            .data(this.barDataPoints);
        const barSelectionMerged = this.barSelection
            .enter()
            .append('rect')
            .merge(this.barSelection);
        barSelectionMerged.classed('bar', true);
        barSelectionMerged
            .attr("width", xScale.bandwidth())
            .attr("height", d => height - yScale(d.value))
            .attr("y", d => yScale(d.value))
            .attr("x", d => xScale(d.category))
            .style("fill", (dataPoint) => dataPoint.color)
            .style("stroke", (dataPoint) => dataPoint.strokeColor)
            .style("stroke-width", (dataPoint) => `${dataPoint.strokeWidth}px`);
        this.barSelection
            .exit()
            .remove();
    }
    static wordBreak(textNodes, allowedWidth, maxHeight) {
        textNodes.each(function () {
            textMeasurementService.wordBreak(this, allowedWidth, maxHeight);
        });
    }
    /**
     * Enumerates through the objects defined in the capabilities and adds the properties to the format pane
     *
     * @function
     * @param {EnumerateVisualObjectInstancesOptions} options - Map of defined objects
     */
    enumerateObjectInstances(options) {
        let objectName = options.objectName;
        let objectEnumeration = [];
        if (!this.barChartSettings ||
            !this.barChartSettings.enableAxis ||
            !this.barDataPoints) {
            return objectEnumeration;
        }
        switch (objectName) {
            case 'enableAxis':
                objectEnumeration.push({
                    objectName: objectName,
                    properties: {
                        show: this.barChartSettings.enableAxis.show,
                        fill: this.barChartSettings.enableAxis.fill,
                    },
                    selector: null
                });
                break;
            case 'colorSelector':
                for (let barDataPoint of this.barDataPoints) {
                    objectEnumeration.push({
                        objectName: objectName,
                        displayName: barDataPoint.category,
                        properties: {
                            fill: {
                                solid: {
                                    color: barDataPoint.color
                                }
                            }
                        },
                        propertyInstanceKind: {
                            fill: 3 /* ConstantOrRule */
                        },
                        altConstantValueSelector: barDataPoint.selectionId.getSelector(),
                        selector: dataViewWildcard.createDataViewWildcardSelector(0 /* InstancesAndTotals */)
                    });
                }
                break;
        }
        ;
        return objectEnumeration;
    }
}
BarChart.Config = {
    xScalePadding: 0.1,
    solidOpacity: 1,
    transparentOpacity: 1,
    margins: {
        top: 0,
        right: 0,
        bottom: 25,
        left: 30,
    },
    xAxisFontMultiplier: 0.04,
};
